






















import {
  Component, Prop, Vue, Emit,
} from 'vue-property-decorator';

@Component
export default class Select extends Vue {
  @Prop() private name!: string

  @Prop() private label!: string

  @Prop() private options!: []

  selected = '';

  @Emit('onChange')
  onChange() {
    return this.selected;
  }
}
