















import {
  Component, Prop, Vue, Emit,
} from 'vue-property-decorator';

@Component
export default class CheckBox extends Vue {
  @Prop() private id!: string

  @Prop() private name!: string

  @Prop() private label!: string

  @Prop() private checkedJob!: []

  jobs = [];

  @Emit('addJob')
  add() {
    return this.jobs[0];
  }
}
